import React from 'react';
//import _uniqueId from 'lodash/uniqueId';
import data from './data.json';
import { Label } from './components/Label';
import './App.css';


/*
классовая тупизна - победила. Будущее настоящее эрэфии - дворянское мракобесие, со всеми вытекающими тупиками
*/

interface LocalStore {
  totalQuetions: number;
  rightAnswers: number;
  levelOfKnown: number;
}

interface JsonData {
  qText: string;
  qCode: string;
  answers: string[];
  hint: string | string[];
  typos: string;
  id: number;
}


class Quiz extends React.Component<any, any> {

  constructor(props: any){
    super(props);

    this.state = {
      startPoint: 0,
      jsonData: this.getUniqueData(data),
      activeItem: false,
      isGoing: false,
      quiz: Array(this.getUniqueData(data).length).fill(null),
      totalRightAnswer: 0,
      totalPercentage: "---",
      totalQuestions: this.getUniqueData(data).length,
      isChecked: Array(this.getUniqueData(data).length).fill(false),
    };

    this.onCheckedAnswer = this.onCheckedAnswer.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.loadQuiz = this.loadQuiz.bind(this);
  }

  _uniqArray(unit: number, start: number = 0, end: number = 12) {
    const nums = Array(unit).fill('').map( (_, index) => index + 1);
    nums.sort(() => Math.random() - 0.5);
    return nums.slice(start, end);
  }

  getUniqueData(array: JsonData[]): JsonData[] {
    //const startos: number = this.state.startPoint;
    //Error: start = undefined
    const ua = this._uniqArray(array.length, 0, array.length-1);//array.length-1
    const getUniqData = array.filter( (el: any, idx: number) => {
      return ua.includes(idx+1) && el
    });
    return getUniqData;
  }

  setCheckArray: any = (cur: any, defValue: null | boolean, action: string) => {
    let newArray: any = [];
    [].forEach.call(cur.children, (el: any, idx: number) => {
      let input = el.children[0].checked;

      if( input === true ){
        newArray[idx] = el.children[0][action]
      } else {
        newArray[idx] = defValue;
      }
    });

    if( newArray.filter((el: any) => el===defValue).length === cur.children.length ) {
      return newArray = defValue;
    }
    return newArray;
  }

  onCheckedAnswer: any = (event: any) => {
    const target = event.target;
    const index = Number(target.name);
    let curTarget = event.currentTarget.parentNode.parentNode;
    const value = target.type === 'radio' ? target.value : this.setCheckArray(curTarget, null, 'value');
    const checker = target.type === 'radio' ? target.value : this.setCheckArray(curTarget, false, 'checked');

    let newQuiz = this.state.quiz;
    let newChecked = this.state.isChecked;
    newQuiz[index] = value;
    newChecked[index] = checker;

    let isGo: boolean = !newQuiz.includes(null);
    this.setState({ 
      quiz: newQuiz,
      isChecked: newChecked,
      isGoing: isGo,
    });
    console.log(this.state.quiz, this.state.isChecked, this.state.isGoing);
  }

  handleSubmit = (event: any) => {
    let quiz = this.state.quiz;
    //if (quiz.length < 1 || quiz.length !== data.length) {
    if (quiz.length < 1 || quiz.length !== this.state.jsonData.length) {
      return event.preventDefault();
    }

    //if( quiz.length === data.length && !quiz.includes(null) ){
    if( quiz.length === this.state.jsonData.length && !quiz.includes(null) ){
      this._sumArray(quiz);
      setTimeout( () => {
        this.linkHistory();
      }, 500)
    }

    event.preventDefault();
  }

  _sumArray(quiz: any[]): number[]{
    let resArr: number[] = [];
    let hintArr: any[] = [];
    //data.forEach( (item) => {
    this.state.jsonData.forEach( (item: any ) => {
      hintArr.push(item.hint);
    });

    quiz.forEach( (elem, index) => {
      if( typeof elem === 'string' ){
        (elem.includes(hintArr[index])) ? resArr.push(1) : resArr.push(0);
      } else {
        resArr.push(this._sumToAnswer(hintArr[index], elem));
      }
    });

    this.toggleActiveItem();
    this.setState({ isGoing: false });

    let summary = resArr.reduce( (s, el) => {
      return s + el
    }, 0);
    let totalResult = Math.round(summary / quiz.length * 100);

    this.setState({
      totalPercentage: totalResult,
      totalRightAnswer: resArr.filter(el => el>0).length,
    });
    return resArr;
  }

  _sumToAnswer(hint: string[], quiz: any[]) {
    const koef = (1 / hint.length);
    let rightAnswer: number = 0;

    quiz.forEach( (num) => {
      hint.forEach( (h) => {
        h.includes(num) ? rightAnswer += koef : rightAnswer += 0;
      });
    });
    return rightAnswer;
  }

  saveHistoryData(): any[] {
    if (!localStorage['jsquiz']) localStorage['jsquiz'] = '[]';
    return JSON.parse( localStorage['jsquiz'] );
  }

  linkHistory() {
    let stored: LocalStore = {
      "totalQuetions": this.state.quiz.length,
      "rightAnswers": this.state.totalRightAnswer,
      "levelOfKnown": this.state.totalPercentage,
    }
    let arr: any[] = this.saveHistoryData();
    arr.push( stored );
    let newArr: string = JSON.stringify( arr );
    localStorage['jsquiz'] = newArr;
  }

  toggleActiveItem() {
    this.setState({
      activeItem: !this.state.activeItem,
    });
  }

  loadQuiz() {
    this.setState({
      startPoint: 0,
      jsonData: this.getUniqueData(data),
      activeItem: false,
      isGoing: false,
      quiz: Array(this.getUniqueData(data).length).fill(null),
      totalRightAnswer: 0,
      totalPercentage: "---",
      isChecked: Array(this.getUniqueData(data).length).fill(false),
    });
    this.linkHistory();
    //console.log('load quiz');
  }

  componentDidMount() {
    console.log('componentDidMount');
    this.setState({
      isGoing: false,
      startPoint: 0,
    });
  }

  componentWillUnmount() {
    console.log('componentWillUnmount');
  }

  render() {
    //console.log('state-render: ', this.state);
    return (
      <div>
        <h4 className="App-reader">Read the questions carefully:</h4>
        <form onSubmit={this.handleSubmit} className={`App-form ${this.state.activeItem ? 'showItem' : ''}`}>
          <ul>
            {this.state.jsonData && this.state.jsonData.map( (el: JsonData, i: number) => (
              <li key={i} className={`App-list__li`}>
                <div className={`App-form__title`}>
                  <h4 className="App-code__text">{`${el.id}`} - {el.qText}</h4>
                  <pre className="App-code__box">{el.qCode}</pre>
                </div>
                <div data-index={i} className="App-form__item">
                  <Label 
                    key={i} 
                    go={el} 
                    idx={i} 
                    onChange={this.onCheckedAnswer} 
                    check={this.state.isChecked} />
                </div>
              </li>
            ))}
          </ul>
          <input
            type="submit"
            disabled={!this.state.isGoing}
            value="Summary" />
          <button onClick={this.loadQuiz}>Load next test</button>
        </form>
        <div className={`App-form__result`}>
          <p>Total right answers: {this.state.totalRightAnswer} from {this.state.quiz.length} questions</p>
          <p>Your knowledge of js: {this.state.totalPercentage} %</p>
          <p>Total questions: {this.state.totalQuestions}</p>
        </div>
      </div>
    )
  }
}

export default Quiz;